import React from 'react';
import NoteCount from './NoteCount';
const Notes = (props) => {
    const { artboard, setSelection, notes } = props;
    const getLayer = (id) => {
        if (id === artboard.id) {
            return artboard;
        }
        else {
            return artboard.layers.find((layer) => layer.id === id);
        }
    };
    return (React.createElement("div", { className: 'c-notes' }, Object.entries(notes).length > 0
        ? Object.keys(notes).map((note, index) => (React.createElement(NoteCount, { key: index, onClick: () => setSelection(getLayer(note)), position: getLayer(note).frame, count: notes[note].length })))
        : null));
};
export default Notes;
