import chroma from 'chroma-js';
export const getImage = (images, id) => {
    return images.find((image) => image.id === id);
};
export const getSVG = (svgs, id) => {
    return svgs.find((svg) => svg.id === id);
};
export const cssColor = (color) => {
    return chroma(color).css();
};
export const styleReducer = (combinedStyles) => {
    // removed null styles
    const filtered = combinedStyles.filter((style) => style !== null);
    // return final style object
    return filtered.reduce((styles, style) => {
        return Object.assign(Object.assign({}, styles), style);
    }, {});
};
export const getOrigin = (frame) => {
    const { x, y, width, height } = frame;
    return {
        top: y,
        right: x + width,
        bottom: y + height,
        left: x,
        yCenter: y + height / 2,
        xCenter: x + width / 2
    };
};
export const placeLeft = (selectionOriginLeft, artboardWidth) => {
    if (selectionOriginLeft > artboardWidth / 2) {
        return true;
    }
    else {
        return false;
    }
};
export const placeTop = (selectionOriginTop, artboardHeight) => {
    if (selectionOriginTop > artboardHeight / 2) {
        return true;
    }
    else {
        return false;
    }
};
